@import "../component.scss";

.description{
	max-width: 880px;
	margin: 0 auto 35px;
	font-weight: 700;
	color: #333;
	line-height: 1.5;
}

.program{
	position: relative;
	&-img{
		position: absolute;
		top: 0;
		left: 0;
		width: 250px;
		overflow: hidden;
		&:before{
			content: '';
			display: block;
			padding-bottom: 120%;
		}
		span{
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			display: block;
			text-emphasis: center;
			background-color: #e2e2e2;
			img{
				position: absolute;
				width: 100%;
				min-height: 100%;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				object-fit: cover;
			}
		}
	}
	&-info{
		height: 300px;
		padding-top: 25px;
		padding-left: 290px;
		color: #2a2a2a;
		box-sizing: border-box;
	}
	&-title{
		margin-bottom: 30px;
		font-size: 30px;
		line-height: 1.3;
		strong{
			font-weight: 400;
			vertical-align: middle;
		}
		.category{
			display: inline-block;
			min-width: 80px;
			height: 30px;
			margin-left: 5px;
			line-height: 30px;
			color: #fff;
			font-size: 14px;
			text-align: center;
			vertical-align: middle;
			border-right: 7px solid #a5cf4f;
			background-color: #575757;
		}
	}
	&-desc{
		width: 100%;
		display: block;
		display: -webkit-box;
		margin-bottom: 20px;
		font-size: 18px;
		line-height: 1.3;
		overflow: hidden;
		text-overflow: ellipsis;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
	}
	&-state{
		strong{
			display: inline-block;
			width: 75px;
			font-weight: 400;
			&:after{
				content: ':';
				float: right;
			}
		}
	}
	.btn{
		position: absolute;
		bottom: 0;
		right: 0;
		width: 131px;
	}
}

.program-list{
	margin-bottom: 30px;
	.group{
		position: relative;
		padding: 30px;
		margin-bottom: 0;
		border-bottom: 1px solid #ccc;
		&:first-child{
			border-top: 1px solid #000;
		}
	}
	.program{
		&-info{
			padding-top: 0;
		}
		&-title{
			margin-bottom: 20px;
		}
		&-desc{
			margin-bottom: 35px;
		}
		&-state{
			font-size: 18px;
			strong{
				display: block;
				font-weight: 700;
				&:after{
					display: none;
				}
			}
		}
		.btn{
			left: 290px;
			width: 130px;
		}
	}
}

.program-wrap{
	padding: 30px 0;
	background-color: #f2f2f2;
	.program{
		max-width: 880px;
		margin: 0 auto;
	}
}

.box{
	padding: 65px 0 50px;
	& > .inner{
		position: relative;
		max-width: 880px;
		margin: 0 auto;
	}
	h3{
		margin-bottom: 25px;
		font-weight: 700;
		font-size: 24px;
		color: #333;
	}
	.con{
		margin-bottom: 35px;
		line-height: 1.5;
		color: #333;
	}
	.btns{
		text-align: right;
	}
}

.finance{
	background-color: #fcfcfc;
	& > .inner{
		position: relative;
		max-width: 880px;
		margin: 0 auto;
	}
	.item{
		padding: 55px 0 50px;
	}
	h4{
		color: #333;
		font-weight: 700;
		font-size: 20px;
	}
	.sub-title{
		padding-top: 15px;
	}
	.elements{
		padding-top: 30px;
		li{
			margin-bottom: 15px;
			color: #333;
			strong{
				position: relative;
				display: inline-block;
				width: 95px;
				padding-left: 23px;
				font-weight: 700;
				box-sizing: border-box;
			}
			.flag{
				position: absolute;
				top: 3px;
				left: 0;
				display: inline-block;
				border: 8px solid transparent;
				&-1{
					border-color: #a9c33e transparent transparent #a9c33e;
				}
				&-2{
					border-color: #1f8bcc transparent transparent #1f8bcc;
				}
				&-3{
					border-color: #005fa1 transparent transparent #005fa1;
				}
			}
		}
	}
	.standards{
		@include clear;
		margin-top: 45px;
		li{
			float: left;
			width: 182px;
			color: #333;
			&:last-child{
				width: 149px;
			}
			.icon{
				display: block;
				width: 148px;
				height: 148px;
				border-radius: 50%;
			}
			strong{
				display: block;
				padding-top: 25px;
				padding-left: 30px;
			}
			span{
				display: block;
				padding-top: 10px;
				padding-left: 30px;
				line-height: 1.3;
			}

		}
	}
}



